import React, { useState, useRef, useEffect } from 'react';
import Quiz from './component/Quiz';
import { GlobalStyle, ANI_TIME, MAX_WIDTH } from './common';
import { ElementType, IQuiz, IThink } from './type';
import styled from 'styled-components';
import { Flex, Text } from 'rebass';
import { playStarAnimation } from './animation';
import Modal from './component/Modal';
import Think from './component/Think';

function App() {
  const [quizIndex, setQuizIndex] = useState(0);
  const [star, setStar] = useState(0);
  const [thinkItem, setThinkItem] = useState<IThink | null>(null);
  const [showNext, setShowNext] = useState(false);
  const [faceIn, setFaceIn] = useState(true);
  const [failCount, setFailCount] = useState(0);
  const starRef = useRef<HTMLElement>(null);
  function onFail() {
    setFailCount(failCount + 1);
    setQuizIndex(0);
    setStar(0);
  }
  function onNext() {
    setFaceIn(false);
    setShowNext(true);
    setTimeout(() => {
      setFaceIn(true);
    }, 1);
    setTimeout(() => {
      setShowNext(false);
      setQuizIndex(quizIndex + 1);
    }, ANI_TIME);
    const item = THINK_LIST.find(item => item.quizIndex === quizIndex + 1);
    if (item) {
      setThinkItem(item);
    }
  }
  function increaseStar(justAni?: boolean) {
    if (starRef.current) {
      const bound = starRef.current.getBoundingClientRect();
      const x = bound.left + bound.width / 2 - 40;
      const y = bound.top + bound.height / 2 - 3;
      playStarAnimation({ x, y });
      if (!justAni) {
        setStar(star + 1);
      }
    }
  }
  useEffect(() => {
    increaseStar(true);
  }, []);
  return (
    <Container key={failCount}>
      <Flex p="20px" justifyContent="space-between" fontSize="20px">
        <Text flex="1 0">
          리액트 퀴즈 {1 + Math.floor(quizIndex / 3)}-{(quizIndex % 3) + 1}
        </Text>
        <Text ref={starRef} flex="0 0 auto" textAlign="right">
          {star} / 9
        </Text>
      </Flex>
      {QUIZ_LIST.map(
        (data, index) =>
          ((quizIndex === index - 1 && showNext) || quizIndex === index) && (
            <Quiz
              key={index}
              show={(quizIndex === index - 1 && faceIn) || quizIndex === index}
              onFail={onFail}
              onNext={onNext}
              increaseStar={increaseStar}
              data={data}
            />
          ),
      )}
      <GlobalStyle />
      <Modal
        isOpen={!!thinkItem}
        onRequestClose={() => setThinkItem(null)}
        contentStyle={{
          margin: '10vh auto',
          height: '80vh',
          maxWidth: `${MAX_WIDTH - 60}px`,
          top: 0,
          right: '5vw',
          left: '5vw',
          bottom: undefined,
          boxSizing: 'border-box',
          textAlign: 'center',
        }}
      >
        <Think data={thinkItem} onClose={() => setThinkItem(null)} />
      </Modal>
    </Container>
  );
}

