import React from 'react';
import { Flex, Text, Card } from 'rebass';
import { ICode } from '../type';
import { CONTAINER_PADDING, MAX_WIDTH } from '../common';

interface IProps {
  data: ICode;
}

export default function Code({ data }: IProps) {
  return (
    <Card
      width="100%"
      style={{ maxWidth: MAX_WIDTH - CONTAINER_PADDING * 2 }}
      borderRadius="5px"
      backgroundColor="#272822"
      p="10px"
    >
      <Flex>
        {data.map((item, index) => (
          <Text
            key={index}
            fontWeight="bold"
            color={item.isGreen ? '#a6e22e' : '#f92672'}
          >
            {item.text}
          </Text>
        ))}
      </Flex>
    </Card>
  );
}
